.category {
    background-color: rgb(28, 28, 28);
    width: 100vw;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.category-item {
    height: 9vh;
    width: 9vh;
    margin-left: 10px;
    margin-right: 10px;
    display: inline-block;
    transition: 0.3s;
    display: flex;
    justify-content: center;
}

.category-item:hover {
    background-color: gray;
}

.category-item > img {
    height: 60%;
    margin-top: 20%;
}

.category-selected {
    background-color: white;
    border-radius: 10px;
    width: 7vh;
    height: 3px;
    position: fixed;
    top: 9vh;
}